@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display+SC:wght@400;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700&display=swap');

:root {
	--beige: #ddd0c8;
	--dark: #333333;
	--darker: #1e1e1e;
	--playfair: 'Playfair Display', serif;
	--playfair_sc: 'Playfair Display SC', serif;
	--raleway: 'Raleway', sans-serif;
}

body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: var(--beige);
	color: var(--dark);
	font-family: var(--raleway);
}
.dark-mode {
	background-color: var(--darker);
	color: var(--beige) !important;
}

.home-link {
	position: fixed;
	z-index: 600;
	display: flex;
	width: 3%;
	height: 50vh;
	padding-top: 2%;
	padding-bottom: 30px;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}
.home-item {
	margin-bottom: 40px;
	padding-right: 15px;
	padding-left: 20px;
	flex: 0 auto;
	-webkit-transform: rotate(-90deg);
	-ms-transform: rotate(-90deg);
	transform: rotate(-90deg);
}
.home-item a {
	text-decoration: none;
	color: var(--dark);
	transition: opacity 0.5s ease;
}
.home-item a:hover {
	text-decoration: line-through;
	opacity: 0.69;
}
.home-line {
	width: 1px;
	height: 180px;
	margin-top: 10px;
	margin-bottom: 80px;
	background-color: var(--dark);
}

@font-face {
	font-family: futura;
	src: local('Futura-Bold'), url('./Assets/fonts/futur.ttf') format('truetype');
}
