.about-page1 {
	padding: 2%;
	padding-bottom: 7rem;
}
.about-container {
	padding: 0 4% 0 8%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.about-text-content {
	width: 62%;
}
.about-text-content h1 {
	font-family: var(--playfair);
	font-weight: 700;
	font-size: 64px;
	text-transform: uppercase;
}

.about-text-content p {
	line-height: 30px;
}
.about-text-content #p1 {
	font-family: var(--playfair_sc);
	font-size: 32px;
	text-transform: capitalize;
	letter-spacing: 0.1em;
	line-height: 1.2em;
}

.about-text-content #p2,
#p3,
#p4 {
	width: 90%;
	font-size: 16px;
	font-weight: 300;
	letter-spacing: 0.069em;
}

.about-side-content {
}

.about-my-pic img {
	height: 420px;
}

.about-my-resume {
	display: flex;
	align-items: center;
	justify-content: center;
}

.about-my-resume-btn {
	background-color: var(--dark);
	border: 2px solid var(--beige);
	margin-top: 2.5rem;
	border-radius: 999px;
	width: 280px;
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: background-color 0.5s ease;
}
.about-my-resume-btn:hover {
	/* border-radius: 10px; */
	border: 2px solid var(--dark);
	background-color: var(--beige);
	/* color: var(--dark); */
	transition: background-color 0.5s ease;
}
.about-my-resume-btn:hover h1 {
	color: var(--dark);
}
.about-my-resume-btn h1,
.about-my-resume-btn a {
	font-family: var(--playfair);
	font-style: normal;
	font-weight: 700;
	font-size: 20px;
	letter-spacing: 0.08em;
	color: var(--beige);
	user-select: none;
	text-decoration: none;
}

.page-content {
	padding: 4% 16% 0 8%;
}
.page-content h1 {
	font-family: var(--playfair_sc);
	font-size: 40px;
	font-weight: 400;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	margin-bottom: 2.75rem;
}
.page-content h2 {
	font-family: var(--playfair_sc);
	font-size: 20px;
	font-weight: 400;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	padding-bottom: 0.8rem;
}
.skill,
.experience {
	padding-bottom: 1.75rem;
}
.skill span {
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	letter-spacing: 0.13em;
}
.skill span:not(:last-child) {
	border-right: 1px solid var(--dark);
}

.experience ul {
	letter-spacing: 0.13rem;
	margin-left: 2.25rem;
}
.experience h3 {
	font-weight: 700;
	margin-left: 2rem;
}
.experience h4 {
	font-weight: 500;
	margin-left: 2.15rem;
}
h5 {
	user-select: none;
	text-decoration: underline;
	font-size: 18px;
}
h5:hover {
	text-decoration: line-through;
	opacity: 0.69;
}
.experience span {
	font-weight: 700;
}

.experience a {
	text-decoration: underline;
	color: var(--dark);
	/* font-weight: 600; */
	box-shadow: inset 0 0 0 0 var(--dark);
	margin: 0 -0.25rem;
	padding: 0 0.25rem;
	transition: color 0.5s ease-in-out, box-shadow 0.5s ease-in-out;
}
.experience a:hover {
	box-shadow: inset 420px 0 0 0 var(--dark);
	color: var(--beige);
}

.content-span {
	letter-spacing: 0.08em;
	font-size: 20px;
}

.about-my-photography-btn a {
	text-decoration: underline;
	color: var(--dark);
}
.about-my-photography-btn a:hover {
	opacity: 0.69;
	text-decoration: line-through;
}

/* --------RESPONSIVE ------------ */
@media screen and (max-width: 920px) {
	.about-container {
		margin-bottom: 4rem;
	}
	.about-text-content {
		width: 95%;
	}
	.about-text-content #p2,
	#p3,
	#p4 {
		width: 98%;
	}
	.about-side-content {
		margin-top: 3rem;
	}
	.about-my-pic img {
		width: 80vw;
		height: auto;
	}
}

@media screen and (min-width: 1100px) {
	html {
		scroll-snap-type: y mandatory;
		overflow-y: scroll;
		scroll-behavior: smooth;
	}

	.about-page > section {
		scroll-snap-align: start;
		scroll-snap-stop: always;
	}
	.page {
		height: 100vh;
	}
	.hehehe {
		height: 70vh;
	}
}

@media screen and (max-width: 840px) {
	.about-text-content #p1 {
		font-size: 28px;
	}
	.skill h2 {
		width: 100%;
	}
	.skill {
		display: flex;
		flex-wrap: wrap;
	}
	.page-content {
		padding: 4%;
	}
}
@media screen and (max-width: 768px) {
	.about-container {
		padding-top: 8%;
	}
	.about-text-content #p1 {
		font-size: 26px;
	}
}
