.work-page-container {
	margin: 2%;
}

.work-container {
	padding: 1% 10% 0 10%;
	margin-bottom: 5rem;
}

.work-container h1 {
	font-family: var(--playfair);
	font-weight: 700;
	font-size: 64px;
	text-transform: uppercase;
}

.work-categories {
	margin-top: 4rem;
	font-weight: 400;
	font-size: 16px;
	text-transform: uppercase;
}
.work-categories ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.work-categories li {
	list-style: none;
	margin: 0.5rem 1rem;
	padding: 0.5rem 1rem;
	user-select: none;
	transition: all 1s ease;
}
.work-categories li.active {
	color: var(--beige);
	background-color: var(--dark);
	border-radius: 99px;
}

.work-items {
	margin-top: 5rem;
	margin-left: 2rem;
	text-transform: uppercase;
}

.work-item {
	display: flex;
	margin: 2rem 0;
}

.work-item-names h2 {
	font-family: var(--playfair);
	font-size: 32px;
	font-weight: 400;
	letter-spacing: 0.08em;
	margin: 0;
	margin-right: 2rem;
}
.work-item-names p {
	font-weight: 300;
	letter-spacing: 0.08em;
}

.work-item-year {
	font-size: 16px;
	font-weight: 500;
	margin-left: auto;
	margin-right: 2rem;
	display: flex;
	align-items: center;
}

.designs,
.arts {
	/* margin-top: 8%; */
	width: 69vw;

	-webkit-column-count: 3;
	-moz-column-count: 3;
	column-count: 3;
	-webkit-column-width: 33%;
	-moz-column-width: 33%;
	column-width: 33%;
	padding: 0 12px;
}
.pics,
.d-pics,
.art-pics {
	-webkit-transition: all 1s ease;
	transition: all 1s ease;
	cursor: pointer;
	margin-bottom: 12px;
}
.pics:hover,
.d-pics:hover,
.art-pics:hover {
	filter: opacity(0.69);
}
.pics img,
.art-pics img,
.d-pics img {
	width: 100%;
}
.d-pics {
	position: relative;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.d-pics::after {
	content: '';
	display: block;
	padding-bottom: 100%;
}
.d-pics img {
	position: absolute;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}
/* ---------RESPONSIVE---------- */

@media screen and (max-width: 768px) {
	.work-container h1 {
		font-size: 50px;
	}
	.work-container {
		padding: 1% 2.5% 0 2.5%;
	}
	.work-categories {
		width: 80vw;
	}
	.work-item-names h2 {
		font-size: 26px;
	}
}

@media (max-width: 991px) {
	.designs,
	.arts {
		-webkit-column-count: 2;
		-moz-column-count: 2;
		column-count: 2;
		/* width: 70vw; */
	}
}

@media (max-width: 769px) {
	.designs,
	.arts {
		-webkit-column-count: 1;
		-moz-column-count: 1;
		column-count: 1;
		padding: 0;
		margin-left: 5%;
	}
}
