.contact-page {
	margin: 2%;
}
.contact-logo {
	position: absolute;
	top: 3%;
	right: 5%;
}
.contact-items {
	position: fixed;
	left: 13%;
	top: 30%;
	color: var(--dark);
	width: 90%;
}
.contact-header h1 {
	font-family: var(--playfair);
	font-style: normal;
	font-weight: 400;
	font-size: 72px;
}
.contact-item p {
	font-size: 18px;
	letter-spacing: 0.08em;
	line-height: 28px;
	margin: 0;
}
.contact-item a {
	text-decoration: underline;
	color: var(--dark);
	/* font-weight: 600; */
	box-shadow: inset 0 0 0 0 var(--dark);
	margin: 0 -0.25rem;
	padding: 0 0.25rem;
	transition: color 0.5s ease-in-out, box-shadow 0.5s ease-in-out;
}
.contact-item a:hover {
	box-shadow: inset 420px 0 0 0 var(--dark);
	color: var(--beige);
}
.contact-item {
	margin-top: 54px;
}
.contact-links {
	margin: 0;
	display: flex;
	/* flex-direction: column; */
}
.contact-link-item a {
	color: var(--dark);
	font-weight: 400;
	font-size: 18px;
	letter-spacing: 0.08em;
	text-decoration: underline;
}
.contact-link-item {
	display: flex;
	align-items: center;
}
.contact-link-item:not(:last-child) {
	border-right: 1px solid var(--dark);
}
.contact-link-item {
	padding-right: 0.5em;
	padding-left: 0.5em;
}
.contact-link-items {
	display: flex;
}

/* ---------------- RESPONSIVE --------------------- */
@media screen and (max-width: 768px) {
	.home-link {
		width: 100%;
		height: auto;
		padding-bottom: 0px;
		padding-top: 5%;
		padding-left: 0px;
		flex-direction: row;
		position: relative;
	}
	.home-item {
		margin-bottom: 0px;
		transform: rotate(0deg);
	}
	.home-line {
		width: 100px;
		height: 1px;
		margin-top: 0px;
		margin-bottom: 0px;
	}
	.contact-links p {
		width: 90vw;
	}
	.contact-links {
		display: flex;
		flex-direction: column;
	}
	.contact-link-items {
		display: flex;
		/* flex-direction: column; */
		flex-wrap: wrap;
	}
	.contact-link-item {
		margin-top: 0.69em;
	}
}
