html {
	scroll-behavior: smooth;
}

.about-page4 {
	padding: 2%;
}

.gallery {
	margin-top: 8%;
	width: 69vw;

	-webkit-column-count: 3;
	-moz-column-count: 3;
	column-count: 3;
	-webkit-column-width: 33%;
	-moz-column-width: 33%;
	column-width: 33%;
	padding: 0 12px;
}
.gallery .pics:hover {
	filter: opacity(0.69);
}

.footer {
	margin-bottom: 2%;
	margin-top: 8%;
	user-select: none;
	font-weight: 700;
	text-decoration: underline;
	color: var(--dark);
}
.footer:hover {
	opacity: 0.69;
	text-decoration: line-through;
}

.gallery .pics {
	-webkit-transition: all 1s ease;
	transition: all 1s ease;
	cursor: pointer;
	margin-bottom: 12px;
}
.model {
	width: 100%;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--darker);
	transition: all 0.5s ease;
	visibility: hidden;
	opacity: 0;
	/* transform: scale(0); */
	overflow: hidden;
	z-index: 999;
}

.imgViewer {
	visibility: visible;
	opacity: 1;
}
.model img:not(:last-child) {
	width: auto;
	max-width: 100%;
	height: auto;
	max-height: 100%;
	display: block;
	line-height: 0;
	box-sizing: border-box;
	padding: 45px 0 45px;
	margin: 0 auto;
}
.model img:last-child {
	/* display: none; */
	position: fixed;
	top: 2rem;
	right: 2rem;
}
.model img:last-child:hover {
	opacity: 0.69;
}

@media (max-width: 991px) {
	.gallery {
		-webkit-column-count: 2;
		-moz-column-count: 2;
		column-count: 2;
		width: 85vw;
	}
}

@media (max-width: 480px) {
	.gallery {
		-webkit-column-count: 1;
		-moz-column-count: 1;
		column-count: 1;
		padding: 0;
	}
}
