.homepage {
	margin: 2%;
}
.rye {
	display: flex;
	align-items: center;
	/* justify-content: center; */
}
.ryelogo {
	height: 46px;
}
.ryelogo-home {
	margin-top: 1%;
}
.darkmode {
	width: 40px;
	height: 40px;
	transition: all 0.5s ease-in-out;
}

.topbar {
	margin-left: 5%;
	margin-right: 5%;
	display: flex;
	justify-content: space-between;
}

.leftbar {
	position: fixed;
	top: 50%;
}

.leftbar img {
	width: 24px;
}

.nav-items {
	position: relative;
	top: 50%;
}
.nav-items ul {
	display: flex;
	justify-content: space-between;
	list-style-type: none;
}
.nav-items ul li:not(:last-child) {
	margin-right: 28px;
}
.nav-items ul a {
	font-size: 26px;
	/* font-weight: 400; */
	font-family: var(--playfair);
	text-decoration: none;
	color: var(--dark);
}
.nav-items ul a:hover {
	font-style: italic;
	font-weight: 600;
}

.textcontent {
	letter-spacing: 0.21em;
	/* margin-left: 13%;
	margin-top: 21%; */
	position: fixed;
	left: 15%;
	bottom: 28%;
}
.textcontent h1 {
	font-size: 52px;
	font-weight: 400;
	text-transform: uppercase;
}
.textcontent p {
	font-size: 26px;
	font-weight: 300;
	width: 500px;
}

.downarrow {
	position: fixed;
	left: 47%;
	bottom: 5%;
}

/* -------------------- HOVERS -------------------- */

@media (hover: hover) and (pointer: fine) {
	.darkmode:hover {
		transform: scale(1.25);
	}
	.leftbar img:hover {
		transform: scale(1.25) !important;
	}
}

/* -------------------- RESPONSIVE -------------------- */
@media screen and (max-width: 1100px) {
	.nav-items {
		margin-right: 7%;
	}
}

@media screen and (max-width: 768px) {
	.topbar {
		margin: 8%;
	}
	.leftbar img {
		width: 16px;
	}
	.ryelogo {
		height: 38px;
	}
	.darkmode {
		width: 34px;
		height: 34px;
	}
	.nav-items ul li:not(:last-child) {
		margin-right: 13px;
	}
	.textcontent h1 {
		font-size: 32px;
	}
	.textcontent p {
		font-size: 18px;
		width: 380px;
		letter-spacing: 0.13em;
	}
}

@media screen and (max-width: 700px) {
	/* .ryelogo {
		position: fixed;
		top: 13%;
	} */
	.ryelogo-home {
		margin-top: 6.9%;
	}
	.nav-items {
		margin-right: 0;
		/* margin-top: 9%; */
	}
	.nav-items ul {
		display: flex;
		flex-direction: column;
		margin-left: auto;
		/* margin-left: 5rem; */
	}
	.nav-items li {
		margin-left: auto;
		margin-right: 0 !important;
	}
	.nav-items li a {
		font-size: 38px;
	}
}

@media screen and (max-width: 479px) {
	.ryelogo {
		height: 36px;
	}
	.darkmode {
		width: 32px;
		height: 32px;
	}
	.textcontent h1 {
		font-size: 26px;
	}
	.textcontent p {
		font-size: 16px;
		width: 300px;
		letter-spacing: 0.15em;
	}
}
